body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toolbar{
    position: fixed;
    top: 0;
    left: 0;
    height: 56px;
    width: 100%;
    background-color:#222629 ;
}
.main{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    
    height: 100%;
    
   
}
.main .list ul{
    display: -webkit-flex;
    display: flex;
    list-style:none;
    
}
.main .list ul .link{
    padding: 1rem;
}
.main .list ul .link{
    text-decoration: none;
    color: #ffffff
}
.logo a{
    text-decoration: none;
    color: #ffffff

}
.space{
    -webkit-flex:1 1;
            flex:1 1;
}
.logo{
    font-size: 1.5rem;
    padding-left: 15px;
    
}

.main .list .link:hover{
    background-color: white;
    
}

.color{
    display: none;
    width: 100%;
    height: 35px;
    background-color:black;
     
    
}
@media screen and (max-width:700px){
    
    .toolbar{
        position: fixed;
        top: 0;
        left: 0;
        height: 85px;
        width: 100%;
        
    }
    .main{
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-flex-direction: column;
                flex-direction: column;
        height: 100%;
        
       
    }
    /* .main .list ul li a{
        padding: 0;
        
    } */
    .color{
        display: block;
    }
    .logo a{
        margin: 0;   
        /* text-decoration: underline; */
     }
    .main .list ul{
        padding-left: 0;
    }
    .main .list ul li{
        margin-bottom: 0;
        padding: 0.5rem;
    }
    .logo{
       
        padding-left: 0;
    }
    
    /* .toolbar .main .list{
        margin-top: -10px;
    } */
}

